.main-nav {
  font-weight: 700;
  font-size: 20px;
  line-height: 20px;
  text-transform: uppercase;
  margin: 0;
  position: relative;
  outline: 1px solid orange;
}
@media (min-width: 1220px) {
  .main-nav {
    position: absolute;
    right: 0;
    z-index: 10;
  }
}
.main-nav__toggle {
  position: absolute;
  background-color: transparent;
  border: none;
  display: block;
  top: -42px;
  right: 20px;
  background-image: url("../img/icon-mobile-menu.svg");
  background-repeat: no-repeat;
  background-position: 0 0;
  cursor: pointer;
}
.main-nav--closed .main-nav__toggle {
  width: 24px;
  height: 18px;
  background-image: url("../img/icon-mobile-menu.svg");
  background-repeat: no-repeat;
  background-position: 0 0;
}
.main-nav--opened .main-nav__toggle {
  width: 18px;
  height: 18px;
  right: 23px;
  background-image: url("../img/icon-close.svg");
  background-repeat: no-repeat;
  background-position: 0 0;
}
.main-nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
  outline: 1px solid red;
  min-height: 198px;
  width: 320px;
}
